/**
 * Optimizes Google Play icon URLs by appending/modifying the size parameter.
 * Google's CDN supports parameters like =s128 or =s64 to resize images on the fly.
 * @param {string} url The original icon URL
 * @param {number} size The desired size in pixels
 * @returns {string} Optimized URL
 */
export function optimizeIcon(url, size = 200) {
    if (!url) return url;

    // Safety first: The regex was too aggressive and cutting the ID.
    // We only want to remove the part starting with '=' if it exists.
    // If no '=' exists, we just append it.
    const baseUrl = url.split('=')[0];

    // Also remove the old legacy format modifier (-rw) if it was at the end of the base URL
    const cleanedBaseUrl = baseUrl.replace(/-rw$/, '');

    return `${cleanedBaseUrl}=s${size}-rw`;
}
